<template>
  <div>
    <!---->
    <Breadcrumb class="breadcrumb">
      <BreadcrumbItem>
        <Icon type="ios-home-outline"></Icon>其他设置
      </BreadcrumbItem>
      <BreadcrumbItem class="curBreadcrumb">
        <Icon type="logo-buffer"></Icon> 专家管理
      </BreadcrumbItem>
      <span class="screenContent">
        <img
          style="width: 11px;margin-right: 8px;margin-left: 10px;"
          src="../assets/day.png"
        >
        {{ curTime }}</span>
      <span class="screenContent">
        <img
          style="width: 10.5px;margin-right: 8px"
          src="../assets/screen.png"
        >
        <a
          href="javascript:void(0)"
          @click="isShowScreen"
        >
          筛选
          <Icon type="ios-arrow-down"></Icon>
        </a>
      </span>
    </Breadcrumb>
    <Content
      v-if="isScreen"
      class="content isScreen"
      :style="{padding: '24px', background: '#fff', marginBottom: '10px'}"
    >
      <Form
        :model="screen"
        :label-width="62"
      >
        <Row>
          <Col
            :xs="{ span: 5 }"
            :lg="{ span: 6}"
          >
          <FormItem label="部门">
            <Select
              v-model="screen.yW"
              filterable
            >
              <Option value="beijing">New York</Option>
              <Option value="shanghai">London</Option>
              <Option value="shenzhen">Sydney</Option>
            </Select>
          </FormItem>
          </Col>
          <Col
            :xs="{ span: 11, offset: 1 }"
            :lg="{ span: 6}"
          >
          <FormItem label="资金类别">
            <Select
              v-model="screen.zJ"
              filterable
            >
              <Option value="beijing">New York</Option>
              <Option value="shanghai">London</Option>
              <Option value="shenzhen">Sydney</Option>
            </Select>
          </FormItem>
          </Col>
          <Col
            :xs="{ span: 5, offset: 1 }"
            :lg="{ span: 6 }"
          >
          <FormItem label="主管单位">
            <Select
              v-model="screen.zG"
              filterable
            >
              <Option value="beijing">New York</Option>
              <Option value="shanghai">London</Option>
              <Option value="shenzhen">Sydney</Option>
            </Select>
          </FormItem>
          </Col>
          <Col
            style="text-align: right"
            :xs="{ span: 1}"
            :lg="{ span: 2, offset: 1 }"
          >
          <Button type="primary">
            确认
          </Button>
          </Col>
        </Row>
      </Form>
    </Content>
    <Content
      class="content"
      :style="{padding: '14px 24px', minHeight: '220px', background: '#fff'}"
    >
      <Button
        type="primary"
        icon="md-add"
        @click="selTypeMod = true"
      >
        新增
      </Button>
      <Button
        type="primary"
        icon="ios-create"
      >
        修改
      </Button>
      <Button
        type="primary"
        icon="md-close"
      >
        删除
      </Button>
      <Button icon="md-print">
        打印
      </Button>
      <Button icon="md-exit">
        导出
      </Button>
      <Table
        ref="checkTable"
        highlight-row
        class="table"
        :columns="columns"
        :data="data"
      ></Table>
      <Page
        style="text-align: right"
        :total="100"
        show-sizer
      />
      <!--详情弹出框-->
      <Modal
        v-model="selTypeMod"
        class="selStyle specialWidth"
        :mask-closable="false"
      >
        <p slot="header">
          <span>请设置机构信息</span>
        </p>
        <Tabs
          :animated="false"
          class="innerTabStyle"
          value="name1"
        >
          <TabPane
            label="信息设置"
            name="name1"
          >
            <Form
              ref="formValidateType"
              :model="formValidateType"
              :rules="ruleValidateType"
              :label-width="80"
            >
              <Row>
                <Col span="11">
                <FormItem
                  label="机构名称"
                  prop="name"
                >
                  <Input
                    v-model.trim="formValidateType.name"
                    placeholder="请填写机构名称"
                  ></Input>
                </FormItem>
                </Col>
                <!--<Col span="11"></Col>-->
                <Col
                  span="11"
                  offset="2"
                >
                <FormItem
                  label="机构性质"
                  prop="quality"
                >
                  <Select
                    v-model="formValidateType.quality"
                    :transfer="transferValue"
                    filterable
                    placeholder="请选择机构性质"
                  >
                    <Option value="0">政府机关</Option>
                    <Option value="1">事业法人</Option>
                    <Option value="2">合作组织</Option>
                    <Option value="3">企业法人</Option>
                  </Select>
                </FormItem>
                </Col>
              </Row>
              <Col span="24">
              <FormItem label="备注">
                <Input
                  v-model.trim="formValidateType.remark"
                  type="textarea"
                  :autosize="{minRows: 1,maxRows: 5}"
                  placeholder="请填写备注"
                ></Input>
              </FormItem>
              </Col>
            </Form>
          </TabPane>
          <TabPane
            class="uploadStyle"
            label="附件"
            name="name7"
          >
            <Icon
              size="16"
              type="md-alert"
            />
            <b>请上传与设置信息一致的附件或证明性文件，本系统只接受PDF格式文件，请上传前进行确认。</b>
            <Upload action="//jsonplaceholder.typicode.com/posts/">
              <span>点击此处上传</span>
            </Upload>
          </TabPane>
        </Tabs>
        <p slot="footer">
          <Button
            style="float: left"
            icon="md-print"
          >
            打印
          </Button>
          <Button
            style="float: left"
            icon="md-exit"
          >
            导出
          </Button>
          <Button
            type="primary"
            @click="submitType('formValidateType')"
          >
            确认
          </Button>
          <Button
            style="margin-left: 8px"
            @click="resetType('formValidateType')"
          >
            取消
          </Button>
        </p>
      </Modal>
      <!--/详情弹出框-->
    </Content>

    <!---->
  </div>
</template>

<script>
export default {
  name: 'ExpertManage',
  data() {
    return {
      transferValue: true,
      formValidateType: {
        name: '',
        quality: '',
        remark: ''
      },
      ruleValidateType: {
        name: [
          { required: true, message: '请填写机构名称', trigger: 'blur' }
        ],
        quality: [
          { required: true, message: '请选择机构性质', trigger: 'change' }
        ]
      },
      //
      // 筛选内容
      screen: {
        yW: '',
        zG: '',
        zJ: ''
      },
      isScreen: false,
      curTime: '',
      showContact: false,
      selTypeMod: false,
      cityList: [
        {
          value: 'New York',
          label: 'New York'
        },
        {
          value: 'London',
          label: 'London'
        },
        {
          value: 'Sydney',
          label: 'Sydney'
        },
        {
          value: 'Ottawa',
          label: 'Ottawa'
        },
        {
          value: 'Paris',
          label: 'Paris'
        },
        {
          value: 'Canberra',
          label: 'Canberra'
        }
      ],
      checkText: '提交审核',
      columns: [
        {
          type: 'index',
          title: '序号',
          width: 60,
          align: 'center'
        },
        {
          title: '机构名称',
          key: 'name',
          render: (h, params) => {
            return h('span', {
              style: {
                color: '#2d8cf0',
                cursor: 'pointer'
              },
              on: {
                click: () => {
                  this.showInfo()
                }
              }
            }, '项目一');
          }
        },
        {
          title: '机构性质',
          key: 'age'
        }
      ],
      data: [
        {
          name: 'John Brown',
          age: 18
        },
        {
          name: 'Jim Green',
          age: 24
        },
        {
          name: 'Joe Black',
          age: 30
        },
        {
          name: 'Jon Snow',
          age: 26
        }
      ]
    }
  },
  mounted() {
    var myDate = new Date();
    const time = myDate.toLocaleString('chinese', { hour12: false });
    this.curTime = time.substring(0, 15)
  },
  methods: {
    submitType(name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          // this.$Message.success('Success!');
        }
      })
    },
    resetType(name) {
      this.$refs[name].resetFields();
      this.selTypeMod = false;
    },
    // 筛选按钮
    isShowScreen() {
      this.isScreen = !this.isScreen;
    },
    addInnerData() {
      this.innerData.push({
        name: '新加的',
        age: 18,
        birthday: '919526400000',
        address: '北京市朝阳区芍药居'

      })
    },
    // 弹框表格
    handleEdit(row, index) {
      this.editName = row.name;
      this.editAge = row.age;
      this.editAddress = row.address;
      this.editfenxiang = row.address;
      this.editBirthday = row.birthday;
      this.editIndex = index;
    },
    handleSave(index) {
      this.data[index].name = this.editName;
      this.data[index].age = this.editAge;
      this.data[index].birthday = this.editBirthday;
      this.data[index].address = this.editAddress;
      this.data[index].fenXiang = this.editfenxiang;
      this.editIndex = -1;
    },
    getBirthday(birthday) {
      console.log('birthday: ', birthday);
      const date = new Date(parseInt(birthday));
      const year = date.getFullYear();
      const month = date.getMonth() + 1;
      const day = date.getDate();
      return `${year}-${month}-${day}`;
    },
    // 弹框表格结束
    ok() {

    },
    cancel() { },
    // 打开详情弹出来
    showInfo() {
      this.selTypeMod = true
    },
    showCheck() {
      this.checkText = '正在审核'
      this.$Modal.confirm({
        title: '是否确认提交审核',
        content: '',
        onOk: () => {
          this.$Message.info('Clicked ok');
        },
        onCancel: () => {
          this.$Message.info('Clicked cancel');
        }
      });
    }
  }
}
</script>

<style>
.ivu-drawer-body > div {
  margin: 20px 0;
}
.ivu-drawer-body > div b {
  margin-left: 3px;
}
/*<!--选择弹出框的样式-->*/
.selStyle .ivu-radio-wrapper {
  font-weight: bold;
}
.selStyle .ivu-modal-body {
  line-height: 2rem;
}
.selStyle .ivu-radio-wrapper {
  margin-right: 21px;
}
.ivu-page-options-sizer {
  margin-right: 0;
}
.table {
  margin: 15px 0;
}
.breadcrumb i {
  margin-right: 8px;
}
.ivu-breadcrumb {
  font-size: 12px;
  margin: 20px 0;
}
.ivu-menu-submenu-title {
  font-weight: bold;
}
.ivu-menu-submenu-title span > i,
.ivu-menu-submenu-title > i {
  margin-right: 4px;
}
.ivu-menu-vertical .ivu-menu-item,
.ivu-menu-vertical .ivu-menu-submenu-title {
  font-size: 12px;
  color: #495060;
}
.ivu-menu-light.ivu-menu-vertical .ivu-menu-item-active:not(.ivu-menu-submenu) {
  font-weight: bold;
}
.ivu-menu-submenu.ivu-menu-opened {
  padding-top: 8px;
}
</style>



